.overlay {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1200;

    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    height: 100vh;

    background-color: rgba(0, 0, 0, 0.8);
}

.modal {
    max-width: calc(100vw - 48px);
    height: 95%;
}
